import React from "react";

import s from "./EmptyShoppingBag.module.scss";

export const EmptyShoppingBag: React.FC = () => {
  return (
    <div className={s.body}>
      <h1 className={s.title}>YOUR SHOPPING BAG IS EMPTY</h1>
      <p className={s.message}>
        Add Something To Your Shopping Bag And Come Here Again!
      </p>
    </div>
  );
};
