import React from "react";
import { Link } from "react-router-dom";

import { useAppDispatch, useAppSelector } from "../../hooks";
import { clearShoppingBag } from "../../redux/shoppingBag/slice";
import { selectShoppingBag } from "../../redux/shoppingBag/selectors";
import { SectionTitle, ShoppingBagItem } from "../../components";
import { EmptyShoppingBag } from "../../components/EmptyShoppingBag";

import s from "./ShoppingBag.module.scss";

import arrow from "../../assets/img/backArrowIcon.svg";

const ShoppingBag: React.FC = () => {
  const dispatch = useAppDispatch();
  const { items, totalPrice } = useAppSelector(selectShoppingBag);

  const onClear = () => {
    if (window.confirm("Clear?")) {
      dispatch(clearShoppingBag());
    }
  };

  const bagItemsElem = (
    <>
      <div className={s.header}>
        <Link to="/" className={`${s.linkToHome} ${s.headerItem}`}>
          <img src={arrow} alt="back" />
          BACK TO HOME
        </Link>
        <button
          onClick={onClear}
          className={`${s.clearButton} ${s.headerItem}`}
        >
          CLEAR
        </button>
      </div>
      <div className={s.items}>
        {items.map((obj) => (
          <ShoppingBagItem key={obj.id} {...obj} />
        ))}
      </div>
      <div className={s.total}>
        TOTAL: <span>â‚¬ {totalPrice}</span>
      </div>
    </>
  );

  return (
    <div className={s.page}>
      <div className={`${s.container} container`}>
        <SectionTitle title="SHOPPING BAG" className={s.title} />
        {items.length ? bagItemsElem : <EmptyShoppingBag />}
      </div>
    </div>
  );
};
export default ShoppingBag;
